<template>
<div id="app">
  <div>
    <h1>{{msg}}</h1>
    <h2>{{subject}}</h2>
  </div>
  <div v-for="m in MBTIname" :key="m">
      {{m}}
  </div>
  <div v-for="mt in mbti" :key="mt">
      <div>
      {{mt.flower_name}}
      </div>
      <div>
      {{mt.description}}
      </div>
  </div>
</div>
</template>


<script>

export default {
  name: 'MBTI',
  data() {
    return {
      msg: 'MBTI',
      subject: 'MBTI별 꽃 추천을 해드려요:) 본인의 MBTI를 선택해주세요.',
      MBTIname: ['ESFJ', 'ESTP', 'ESFP', 'ENTJ', 'ENTP', 'ISFJ', 'ENFP', 'ISFP', 'INFP', 'INTJ', 'ISTJ', 'INFJ', 'ENFJ', 'ISTP', 'INTP', 'ESTJ'],
      mbti: []
    }
  },
  methods: {
    test: function() {
    }
  },
  beforeCreate: function() {
    console.log('beforeCreate')

    this.$axios.get('/mbti')
    .then((res) => {
      let list = res.data

      for (var i in list) {
        this.mbti.push(list[i])
      }
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>


